.overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .4);
  z-index: 500;
}

.overlay-container {
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #f5f5f5;
  border-radius: 5px;
  max-width: 80%;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 98vh;
}

.close-overlay {
  position: absolute;
  top: 10px;
  right: 12px;
}

.close-overlay:hover {
  cursor: pointer;
}

.overlay-content {
  padding: 20px;
  height: 100%;
  width: 90%;
  margin: auto;
}

@media only screen and (max-width: 1250px) {
  .overlay-container {
    max-width: 50% !important;
  }
}

@media only screen and (max-width: 750px) {
  .overlay-container {
    max-width: 65% !important;
  }
}

@media only screen and (max-width: 450px) {
  .overlay-container {
    max-width: 80% !important;
  }
}


