#expenses .expenses-list {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  border-left: 5px solid rgb(255, 75, 75);
}

#expenses .exchange-item > hr {
  border: 1px solid #aaa;
  border-radius: 15px;
}

#expenses .exchange-item .exchange-content .exchange-value {
  color: rgb(255, 75, 75);
}

@media only screen and (max-width: 900px) {
  #expenses .expenses-list {
    border: none;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
  }
}


